<template>
<div class="prank">
  <div class="container text-right">
    <b-btn @click="reset" size="lg" variant="danger" v-b-modal.prankModal>à¹à¸à¸¥à¹‰à¸‡à¹€à¸žà¸·à¹ˆà¸­à¸™</b-btn>

    <b-modal class="text-center" ref="prankModal" id="prankModal"
      hide-footer title="à¹à¸à¸¥à¹‰à¸‡à¹€à¸žà¸·à¹ˆà¸­à¸™"
    >
      <h3>à¸‹à¹ˆà¸­à¸™à¸Šà¸·à¹ˆà¸­à¹€à¸žà¸·à¹ˆà¸­à¸™à¸ˆà¸²à¸à¸à¸²à¸£à¸„à¹‰à¸™à¸«à¸²</h3>

      <b-form-input v-model="exclude" style="max-width: 8em" maxlength="4" class="mx-auto text-center"
        size="lg" type="text" placeholder="à¸£à¸«à¸±à¸ª 4 à¸«à¸¥à¸±à¸"
      ></b-form-input>
      <b-form-text>à¸£à¸«à¸±à¸ªà¸‚à¸­à¸‡à¸„à¸™à¸—à¸µà¹ˆà¸•à¹‰à¸­à¸‡à¸à¸²à¸£à¸ˆà¸°à¸‹à¹ˆà¸­à¸™ à¹€à¸Šà¹ˆà¸™: PG03, MK32</b-form-text>

      <b-input-group style="margin-top: 10px">
        <b-form-input ref="prankURL" onclick="this.select()" readonly
          class="form-control" type="text" :value="prankURL"
        ></b-form-input>
        <b-input-group-button slot="right">
          <b-btn @click="copy" v-b-tooltip.hover title="Copy to clipboard">
            <icon name="clipboard"></icon>
          </b-btn>
        </b-input-group-button>
      </b-input-group>
    </b-modal>
  </div>
</div>
</template>

<script>
import { encrypt } from '@/libraries/functions'
import { BASE_URL } from '@/libraries/constants'

export default {
  data () {
    return {
      exclude: ''
    }
  },
  computed: {
    prankURL () {
      return BASE_URL + '?result=' + encrypt(this.exclude)
    }
  },
  methods: {
    reset () {
      this.exclude = ''
    },
    copy () {
      this.$refs.prankURL.$el.select()
      document.execCommand('Copy')
    }
  },
  watch: {
    '$route.query' (query) {
      this.$refs.prankModal.hide()
    }
  }
}
</script>

<style scoped>
</style>



