<template>
<div class="list">
  <b-modal ref="shareModel" hide-footer title="à¹à¸Šà¸£à¹Œà¸«à¸™à¹‰à¸²à¹€à¸§à¹‡à¸šà¸™à¸µà¹‰">
      <div class="d-block text-center">
        <h3>à¸¥à¸´à¸‡à¸à¹Œà¸žà¸£à¹‰à¸­à¸¡à¹à¸¥à¹‰à¸§â€‹ à¹à¸Šà¸£à¹Œà¹€à¸¥à¸¢!</h3>
        <div class="input-group">
          <input ref="shareURL" onclick="this.select()" class="form-control" type="text" :value="shareURL">
          <span class="input-group-btn">
            <b-btn @click="copy" v-b-tooltip.hover title="Copy to clipboard">
              <icon name="clipboard"></icon>
            </b-btn>
          </span>
        </div>
      </div>
  </b-modal>

  <div class="major">
    <div class="container">
      <h2>
        <img v-if="img" :src="img" class="img-fluid" width="100px" alt="">
        {{ major }}
      </h2>
      <hr>
    </div>
  </div>

  <pulse-loader class="text-center" :loading="loading" color="orange"></pulse-loader>

  <h3 v-show="list.length <= 0 && !loading" style="margin-top: 40px" class="text-center text-muted">
    à¸‚à¸­à¸­à¸ à¸±à¸¢ à¹„à¸¡à¹ˆà¸žà¸šà¸‚à¹‰à¸­à¸¡à¸¹à¸¥à¸œà¸¹à¹‰à¸ªà¸¡à¸±à¸„à¸£ à¸žà¸šà¸à¸±à¸™à¹ƒà¸«à¸¡à¹ˆà¸›à¸µà¸«à¸™à¹‰à¸²à¸™à¸°à¸„à¸£à¸±à¸š !
  </h3>
  <div v-show="list.length > 0" class="container mytable">
    <table class="table table-sm">
      <thead class="sticky">
        <tr>
          <td scope="col">à¸£à¸«à¸±à¸ª</td>
          <td scope="col">à¸Šà¸·à¹ˆà¸­à¸ˆà¸£à¸´à¸‡</td>
          <td scope="col">à¸™à¸²à¸¡à¸ªà¸à¸¸à¸¥</td>
          <td scope="col">à¸ªà¸²à¸‚à¸²</td>
          <td scope="col">à¹€à¸§à¸¥à¸²</td>
          <td scope="col">à¹à¸Šà¸£à¹Œ</td>
        </tr>
      </thead>
      <tbody :class="{ cheer: isFound }">
        <tr :tabindex="isFound ? null : 1" onclick="this.focus()"
          class="tr-content" v-for="candidate in list"
        >
          <td>{{ candidate.interviewRef }}</td>
          <td>{{ candidate.firstName }}</td>
          <td>{{ candidate.lastName }}</td>
          <td>
            {{ candidate.major.substr(0, 1).toUpperCase() + candidate.major.substr(1) }}
          </td>
          <td>à¸šà¹ˆà¸²à¸¢</td>
          <td style="padding-top: 6px">
            <social-sharing inline-template
              :url="`https://kykungz.github.io/ywc15-announcement/#/?id=${candidate.interviewRef}`"
              hashtags="ywc15,YoungWebmasterCamp15"
              class="d-inline"
            >
              <div class="social-icon">
                <network network="facebook">
                  <icon style="cursor: pointer" name="facebook-official"></icon>
                </network>
                <network network="twitter">
                  <icon style="cursor: pointer" name="twitter"></icon>
                </network>
              </div>
            </social-sharing>
            <span @click="share(candidate.interviewRef)" class="d-inline share">
              <icon style="cursor: pointer" name="share-alt"></icon>
            </span>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</div>
</template>

<script>
import { BASE_URL } from '@/libraries/constants'
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'

export default {
  name: 'List',
  props: ['major', 'list', 'img', 'loading'],
  components: { PulseLoader },
  data () {
    return {
      shareURL: ''
    }
  },
  computed: {
    isFound () {
      return this.list.length === 1
    }
  },
  methods: {
    share (id) {
      this.shareURL = BASE_URL + `?id=${id}`
      this.$refs.shareModel.show()
    },
    copy () {
      this.$refs.shareURL.select()
      document.execCommand('Copy')
    }
  },
  watch: {
    '$route.query' (query) {
      this.$refs.shareModel.hide()
    }
  }
}
</script>

<style scoped>
.major {
  position: sticky;
  top: 80px;
  background: linear-gradient(to bottom, white, white, rgba(255, 255, 255, 0.9));
  word-wrap: break-word;
  padding-top: 10px;
  z-index: 30;
}

hr {
  margin-top: 0;
}

img {
  margin-top: -10px;
}

th {
  border-top-color: white;
  border-bottom-color: red;
}

thead {
  background: #e2e2e2;
  font-size: 1.2em;
}

.tr-content {
  font-weight: lighter;
  transition: all 200ms;
}

.tr-content:hover {
  background: #f2f2f2;
  transform: scale(1.1);
}

.tr-content:focus {
  background: orange;
  transform: scale(1.1);
  outline: none;
}

.tr-content:focus .social-icon, .tr-content:focus .share{
  color: #333333;
}

.tr-content:hover .social-icon, .tr-content:hover .share{
  color: #333333;
}

.social-icon, .share {
  color: #9d9d9d;
  transition: color 400ms;
}

.cheer {
  background: orange;
  font-size: 2em;
  animation-name: pulse;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

.cheer:hover {
  animation: none;
}

.cheer .tr-content:hover {
  background: rgb(255, 122, 37);
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

@media (max-width: 1024px) {
  .cheer {
    animation: none;
    font-size: 1em;
  }
}

@media (max-width: 400px) {
  .mytable {
    overflow-x: scroll;
  }
}

</style>



